import Module from 'core/module';
import Swiper from 'swiper';
class Testimonials extends Module {
    constructor(el) {
        super(el);
    }
    init() {
        new Swiper('.testimonials .swiper-container', {
            loop: true,
            autoplay: 5000,
            grabCursor: true,
            pagination: '.testimonials .swiper-pagination'
        });
    }
    bindEventListeners() {
    }
}
export default Testimonials;
